<template>
    <div class="maxPlay" v-loading="$store.state.loading">
        <img src="../../../assets/max_bg2.png" alt="">
        <div class="play_max_video" v-if="videoMax != ''&&showMaxTitle == '播放素材'">
            <video-player :options="videoOptions1"></video-player>
        </div>
        <div class="max_main">
            <!--开始打分-->
            <div style="height: 123px;">
                <div class="start_score" v-if="showMaxTitle == '开始打分'">
                    <div>正在打分中
                        <div class="loading">
                            <div class="object" id="object_one"></div>
                            <div class="object" id="object_two"></div>
                            <div class="object" id="object_three"></div>
                        </div>
                    </div>
                </div>
                <div class="max_title" v-if="showMaxTitle != '开始打分'">
                    <div>{{ showMaxTitle }}</div>
                </div>
                <!--倒计时-->
                <div>
                    <div class="max_clock" v-if="showMaxClock">
                        <span>{{max_time}}</span>
                        <i class="iconfont el-icon-emergency-daojishi"></i>
                    </div>
                </div>
            </div>
            <!--播放素材-->
            <div class="material_max" v-if="showMaxTitle == '播放素材'">
                <p class="text_max" v-if="videoMax == ''&&pictureMax == ''">{{ textMax }}</p>
                <!--<div class="play_video" v-if="videoMax != ''">-->
                    <!--<video-player :options="videoOptions1"></video-player>-->
                <!--</div>-->
                <div class="show_img" v-if="pictureMax != ''">
                    <img :src="$store.state.file_path + pictureMax">
                </div>
            </div>
            <!--播放题目 开始答题-->
            <div class="max_subject" v-if="showMaxTitle == '播放题目'||showMaxTitle == '开始答题'">
                <h3>请阅读下列题目</h3>
                <p>本题 {{ subject.score }} 分</p>
                <div>{{ subject.describe }}</div>
                <div v-if="showMaxTitle == '开始答题'" style="text-align: center;font-size: 24px;color: #fff;
                margin-top: 20px;"><span style="color: #f00;">{{ answerCounts }}</span> 人已经完成答题!</div>
            </div>
            <div v-if="showMaxTitle == '开始打分'" style="text-align: center;font-size: 24px;color: #fff;
                font-weight: 500;"><span style="color:#f00;">{{ scoreUserCount }}</span> 位评分人已经完成打分!</div>
            <!--参考答案-->
            <div style="padding-top: 30px;" v-if="showMaxTitle =='显示所有组答案'||showMaxTitle == '开始打分'">
                <div class="max_reference" :style="'border-left: 10px solid ' + maxRandomColor(4)" v-if="showMaxReference">
                    <div class="reference_title">参考答案</div>
                    <div class="reference_content">
                        <!--<span :class="showMaxReferenceDetail ? '': 'showAllReferenceContent'">{{ reference }}</span>-->
                        <div :class="showMaxReferenceDetail ? '': 'showAllReferenceContent'">
                            <span>{{ maxReference }}</span>
                        </div>
                        <p class="answer_detail" v-if="!showMaxReferenceDetail" @click="showMaxReferenceDetail = true"><span>显示全部详情>></span></p>
                        <p class="answer_detail" v-if="showMaxReferenceDetail" @click="showMaxReferenceDetail = false"><span>隐藏全部详情>></span></p>
                    </div>
                </div>
            </div>
            <!--所有组答案-->
          <!--v-if="showMaxTitle == '显示所有组答案'||showMaxTitle == '开始点评'||showMaxTitle == '开始打分'"-->
            <ul class="max_all_answer" v-if="showMaxTitle == '显示所有组答案'||showMaxTitle == '开始打分'">
                <li v-for="(item,index) in allAnswer" :key="index" :style="'border-left: 10px solid ' + maxRandomColor(index)">
                    <div class="group_name">{{ item.name }}</div>
                    <div class="answer">
                        <div :class="item.show ? 'answer_roughly': ''">
                            <span>{{ item.answer }}</span>
                        </div>
                        <p class="answer_detail" v-if="item.show"><span @click="maxAnswerDetail(item.id,item.show)">显示全部详情>></span></p>
                        <p class="answer_detail" v-if="!item.show"><span @click="maxAnswerDetail(item.id,item.show)">隐藏全部详情>></span></p>
                    </div>
                </li>
            </ul>
            <!--开始点评-->
            <div class="max_comment" v-if="showMaxTitle == '开始点评'">
                <video-player :options="videoOptions"></video-player>
            </div>
            <!--展示得分-->
            <div class="show_score" v-if="showMaxTitle == '展示得分'">
                <div class="score_title">得分排行榜</div>
                <p class="score_link">本环节得分</p>
                <ul class="score_group">
                    <li v-for="(item,index) in scoreLinkGroup" :key="index">
                        <p>{{ item.name }}</p>
                        <span>{{ item.score | markStatus1 }}分</span>
                    </li>
                </ul>
                <h3>本次演练总排名</h3>
                <div class="score_main">
                    <div class="every_group" v-for="(item,index) in maxScoreRank" :key="index">
                        <div style="float: left;">
                            <img :src="item.src" alt="">
                            <span class="ranking">第 <b>{{ index + 1 }}</b> 名</span>
                            <span class="group_name">{{ item.name }}</span>
                        </div>
                        <!--<span class="bac" :style="'min-width:60px; width:'+ (item.group_score/scoreMaxTotal * 300) + 'px'">{{ item.group_score }}分</span>-->
                        <div style="float: right;width: 420px;">
                            <el-progress :text-inside="true" :stroke-width="18" :percentage="(item.group_score/scoreMaxTotal)*100" status="success"></el-progress>
                            <span style="margin-left: 10px;margin-right: 0;margin-top: 6px;">{{ item.group_score | markStatus1  }}分</span>
                        </div>
                    </div>
                </div>
            </div>
            <!--结束演练-->
            <div class="max_over" v-if="showMaxTitle == '结束演练'">
                <div class="over_drill_title">本次演练结束</div>
                <div class="the_fourth">前{{ rankMaxIndex }}甲</div>
                <div class="score_main">
                    <div class="every_group" v-for="(item,index) in maxScoreRank" :key="index">
                        <div style="float: left;">
                            <img :src="item.src" alt="">
                            <span class="ranking">第 <b>{{ index + 1 }}</b> 名</span>
                            <!--<span class="bac" :style="'min-width:60px; width:' + (item.group_score/scoreMaxTotal * 300) + 'px'">{{ item.group_score }}分</span>-->
                            <span class="group_name">{{ item.name }}</span>
                        </div>
                        <div style="float: right;width: 420px;">
                            <el-progress :text-inside="true" :stroke-width="18" :percentage="(item.group_score/scoreMaxTotal)*100" status="success"></el-progress>
                            <span style="margin-left: 10px;margin-right: 0;margin-top: 6px;">{{ item.group_score | markStatus1  }}分</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
  import 'video.js/dist/video-js.css'
  import { videoPlayer } from 'vue-video-player'
export default {
  components: {
    videoPlayer
  },
    data(){
        return{
            project_id: '',           //项目id
            maxNode: [],              //环节内容
            maxNodeIndex: '',         //第几环节
            showMaxTitle: '',         //当前步骤
            //素材（文字 图片 视频）
            videoMax: '',
			videoOptions1: {//观看视频参数  IE10不兼容  改用这个
				muted: false,
				language: 'en',
				autoplay: true,
				sources: [{
					type: "video/mp4",
					src: ""
				}],
			},
            pictureMax: '',
            textMax: '',
            //题目
            subject: {
                id: '',     //问题id？
                score: "",    //分数
                describe: "", //题目内容
                answer: '',   //参考答案
            },
            answerCounts: 0,     //已经答题人数
            //参考答案
            maxReference: "",
            showMaxReference: false,//是否显示参考答案
            showMaxReferenceDetail: false,//是否全部显示
            //所有参演组答案
            allAnswer: [],
            scoreUserCount: 0,      //已经完成评分的人数
            //展示得分
            scoreLinkGroup: [],//当前环节得分
            maxScoreRank:[],//排名
            scoreMaxTotal: 0,//演练总分
            rankMaxIndex: '', //前n甲
            rankStatus: false,  //是否获取到前几甲数据
                //定时器
            timeStatus: '',
            //倒计时
            showMaxClock: false,    //是否显示定时器
            max_time: '',       //显示的倒计时时间
            max_timer: '',      //倒计时定时器
            maxDownTime: 0,     //倒计时的时间
            flags: false,
          videoOptions: {//观看直播参数
            sources: [{
//            type: "application/x-mpegURL",
//            src: 'http://pili-live-hls.jiaxingyanlian.com/emergency-drill/php-sdk-test1517380263.m3u8',
              type: "rtmp/mp4",
              src: "",

              withCredentials: false
            }],
            techOrder:['flash'],
            language: 'zh-CN',
            live: true,
            autoplay: true,
            poster:"./static/waitingLive.jpeg",
            height: 1000
          }
        }
    },
    created () {
		this.$store.state.loading = true;
        this.project_id = localStorage['drill_project'];
        this.getMaxLink();      //获取环节
        this.getMaxGroup();     //获取分组
    },
    mounted() {
//    	this.getMaxDrillStatus();
        this.timeStatus = setInterval(()=>{
            this.getMaxDrillStatus();
            if(this.showMaxTitle == '显示所有组答案'||this.showMaxTitle == '开始点评'||this.showMaxTitle == '开始打分'){
            	this.maxAnswerStatus();
            }
            if(this.showMaxTitle == '开始答题'){
				this.getAnswerCount();
			}
			if(this.showMaxTitle == '开始打分'){
				this.checkRecordCount();
			}
			if(this.showMaxTitle == '开始点评'){
				this.videoOptions.sources[0].src = "rtmp://pili-live-rtmp.jiaxingyanlian.com/emergency-drill/php-sdk-test1517368506" ;
//				console.log(this.videoOptions.sources[0].src)
			}
        },2000)

    },

	beforeDestroy(){
		clearInterval(this.max_timer);
	},
    methods:{
	    //格式化时间
	    NewDate(str) {
		    //首先将日期分隔 ，获取到日期部分 和 时间部分
		    var day = str.split(' ');
		    //获取日期部分的年月日
		    var days = day[0].split('-');
		    //获取时间部分的 时分秒
		    var mi = day[day.length - 1].split(':');
		    //获取当前date类型日期
		    var date = new Date();
		    //给date赋值  年月日
		    date.setUTCFullYear(days[0], days[1] - 1, days[2]);
		    //给date赋值 时分秒  首先转换utc时区 ：+8
		    date.setUTCHours(mi[0] - 8, mi[1], mi[2]);
		    return date;
	    },
        //获取演练分组
        getMaxGroup(){
            let self = this;
            this.$axios.post('/api/group-start',{
              design_id: this.project_id
            }).then(function (response) {
    //                console.log(response)
              self.groupLists = response.data.data;
            }).catch(function () {
            })
        },
        //全部答案li左边框随机色
        maxRandomColor(index){
			let colorLists = ["#f2c840", "#50e3c2", "#e38df5", "#80c6f7", "#a1eaff","#f2c840", "#50e3c2", "#e38df5", "#80c6f7", "#a1eaff",
				"#f2c840", "#50e3c2", "#e38df5", "#80c6f7", "#a1eaff","#f2c840", "#50e3c2", "#e38df5", "#80c6f7", "#a1eaff",
				"#f2c840", "#50e3c2", "#e38df5", "#80c6f7", "#a1eaff","#f2c840", "#50e3c2", "#e38df5", "#80c6f7", "#a1eaff"];
//            return colorLists[Math.floor(Math.random()*colorLists.length)];
			return colorLists[index];
        },
        //获取演练环节内容
        getMaxLink(){
            let self = this;
            this.$axios.post('/api/start',{
                design_id: this.project_id
            }).then(function (response) {
                //console.log(response);
                if(response.data.status == 0){
                    self.maxNode = response.data.data.node_name;
                }
            }).catch(function (error) {
            })
        },
        //获取演练状态
        getMaxDrillStatus(){
            let maxStatus = {};
            this.$axios.post('/api/look-status',{
              design_id: this.project_id,
            }).then((response)=>{
//                console.log(response);
                if(response.data.data.design_location){
                    maxStatus = JSON.parse(response.data.data.design_location);
                    //判断是否出现新的步骤状态
                    if(maxStatus.step == this.showMaxTitle){
                    } else {
                    	if(maxStatus.step == '开始点评'){
							this.$store.state.loading = false;
                        } else {
							this.$store.state.loading = true;
                        }
                        //根据环节id 获取该环节步骤
                        for(let i in this.maxNode){
                            if(this.maxNode[i].id == maxStatus.node_id){
                                this.maxNodeIndex = i;
                            }
                        }
                        //获取步骤
                        this.showMaxTitle = maxStatus.step;
                        if(this.showMaxTitle == "播放素材"){
                            //获取的文字素材和文件素材
							this.getMaterials();
                            this.textMax = this.maxNode[this.maxNodeIndex].description;
                        } else if(this.showMaxTitle == "播放题目" ||this.showMaxTitle == "开始答题"){
                            this.getMaxSubjects();
                        } else if(this.showMaxTitle == "显示所有组答案"){
                            this.getMaxSubjects();
                            this.getMaxGroup();    //获取分组
                        } else if(this.showMaxTitle =='开始点评'){
                        	console.log("点评")
                            this.getMaxSubjects();
                        } else if(this.showMaxTitle =='开始打分'){
                            this.getMaxSubjects();
                        } else if(this.showMaxTitle == '展示得分'){
                            this.showMaxReference = false;
                            this.getTotalPoints();//获取总分
                            this.getMaxScore();
                            this.getMaxRank();
                        } else if(this.showMaxTitle == '结束演练'){
                            if(!this.rankStatus){
                                this.getTotalPoints();//获取总分
                                this.getMaxRank();
                                this.rankStatus = true;
                            }
                        }
                        //获取时间
						clearInterval(this.max_timer);
						//是否有倒计时的目标时间
						if(maxStatus.end_time){
							this.$axios.post('/api/time-end')
								.then((response) => {
									let now = this.NewDate(response.data.data);
									this.maxDownTime = (maxStatus.end_time - now)/1000;
									if(this.maxDownTime <= 0){
										this.flags = false;
										clearInterval(this.max_timer);
                                        this.$store.state.loading = true;
                                        //进行下一步
									} else {
										this.max_timer = setInterval(()=>{
											if(this.flags == true){
												this.$store.state.loading = true;
												clearInterval(this.max_timer);//时间到
												//进行下一步
											}
											this.timeDowns();
										},1000);
									}
								}).catch((err)=>{})
						} else {
							this.showMaxClock = false;
							clearInterval(this.timer);
						}
                    }

                } else {
                }
            }).catch((err)=>{})
        },

		//倒计时
		timeDowns () {
			let leftTime = parseInt(this.maxDownTime);
			//let d = parseInt(leftTime/(24*60*60));
			//let h = this.formate(parseInt(leftTime/(60*60)%24));
			let m = this.formate(parseInt(leftTime/60%60));
			let s = this.formate(parseInt(leftTime%60));
			this.showMaxClock = true;
			if(leftTime <= 0){
				this.flags = true;
				this.$emit('time-end');
			}
			if(leftTime < 0 ){
				this.showMaxClock = false;
			}
			this.max_time = `${m}:${s}`;
			this.maxDownTime--;
		},
		formate (time) {
			if(time>=10){
				return time
			}else{
				return `0${time}`
			}
		},
        //根据环节id获取环节素材
        getMaterials(){
            let self = this;
            let videoArr = '';
            let picArr = '';
            this.$axios.post('/api/play-video',{
              node_id: this.maxNode[this.maxNodeIndex].id,
            }).then(function (response) {
    //                console.log(response);
                let ImgType=["gif", "jpeg", "jpg", "bmp", "png"];//图片
                for(let i in response.data.data){
                  if(!RegExp("\.(" + ImgType.join("|") + ")$", "i").test(response.data.data[i].file_path.toLowerCase())){
                    videoArr = response.data.data[i].original_file;
                  } else {
                    picArr = response.data.data[i].original_file;
                  }
                }
                self.videoMax = videoArr;
                self.videoOptions1.sources[0].src = self.$store.state.file_path + videoArr;
				self.pictureMax = picArr;
                self.$store.state.loading = false;
            }).catch(function (error) {
            })
        },
        //根据环节id获取环节题目
        getMaxSubjects(){
            let self = this;
            this.$axios.post('/api/play-problem',{
                node_id: this.maxNode[this.maxNodeIndex].id,
            }).then(function (response) {
//                console.log(response);
                self.subject = response.data.data[0].problem;
//                if(self.showMaxTitle == '显示所有组答案'||self.showMaxTitle =='开始点评'||self.showMaxTitle == "开始打分"){
                if(self.showMaxTitle == '显示所有组答案'||self.showMaxTitle == "开始打分"){
//					console.log("答案 打分")
                    if(self.subject.answer != null){
                        self.showMaxReference = true;
                        //有参考答案
                        self.maxReference = self.subject.answer;
                        //查看参考答案是否显示
						self.$axios.post('/api/problem-status',{
							problem_id: self.subject.id,
						}).then((response)=>{
//						        console.log(response)
							if(response.data.data.status == 1){
								//显示参考答案
								self.showMaxReference = true;
								self.$nextTick(()=>{
									let ref = document.getElementsByClassName('reference_content');
									let spans = ref[0].childNodes[0].childNodes[0];
									if(spans.offsetHeight < 75){
										ref[0].setAttribute('class', 'reference_content have_not');
									}
								})
							} else{
								self.showMaxReference = false;
							}
						}).catch((err)=>{})
                    } else {
//						self.$store.state.loading = false;
					}
                    self.getMaxAnswer();//获取所有组答案
                } else if(self.showMaxTitle == '开始答题'){
//					console.log("开始答题")
                } else if(self.showMaxTitle =='开始点评'){
//					console.log("开始点评")
					self.$store.state.loading = false;
                } else {
					//是播放题目
//					console.log("是播放题目")
					self.$store.state.loading = false;
                }
            }).catch(function (error) {
            })
        },
        //根据演练id 问题id获取已经回答问题的人数
		getAnswerCount(){
			this.$axios.post('/api/answer-user',{
				design_id: this.project_id,
				problem_id: this.subject.id
			}).then((response)=>{
//        		console.log(response.data.data.length);
				this.answerCounts = response.data.data.length;
				this.$store.state.loading = false;
			}).catch((err)=>{})
		},
		//查看大屏上参考答案的显示与否的状态
		maxAnswerStatus(){
			//掉接口
			let aid = '';
			this.$axios.post('/api/play-problem',{
				node_id: this.maxNode[this.maxNodeIndex].id,
			}).then((res)=>{
//				console.log(res)
				if(res.data.data){
					aid = res.data.data[0].problem.id;
					this.$axios.post('/api/problem-status',{
						problem_id: aid,
					}).then((response)=>{
//						console.log(response)
						if(response.data.data.status == 1){
							//显示参考答案
							this.showMaxReference = true;
							this.$nextTick(()=>{
								let ref = document.getElementsByClassName('reference_content');
								let spans = ref[0].childNodes[0].childNodes[0];
								if(spans.offsetHeight < 75){
									ref[0].setAttribute('class', 'reference_content have_not');
								}
							})
						} else{
							this.showMaxReference = false;
						}
					}).catch((err)=>{})
				}
			}).catch((error)=>{})
		},

        //根据问题id 获取所有组答案
        getMaxAnswer(){
            let self = this;
            this.allAnswer = [];
            let arrGroup = this.groupLists;
            this.$axios.post('/api/play-answer',{
              problem_id: this.subject.id,
            }).then(function (response) {
//                console.log(response);
                //判断该演练是否分组
                if(arrGroup[0].name == '此演练不分组'){
                    let arr = [];
                    for(let i in response.data.data){
                        arr.push({
                            name: response.data.data[i].user.name,
                            answer: response.data.data[i].answer,
							answer_id: response.data.data[i].id,
                            show: true
                        })
                    }
                    arrGroup = arr;
                } else {
                    //多组
                    for(let i in response.data.data){
                        for(let x in arrGroup){
                            arrGroup[x].show = 1;
                            if(response.data.data[i].user_id == arrGroup[x].group_user){
                                arrGroup[x].answer = response.data.data[i].answer;
                                arrGroup[x].answer_id = response.data.data[i].id;
                            }
                        }
                    }
                }
                //去除没有回答的小组
                let arrResult = [];
                for(let i in arrGroup){
                    if(arrGroup[i].answer_id){
                        arrResult.push(arrGroup[i]);
                    }
                }
                self.allAnswer = arrResult;
                //查看答案内容长度
                self.$nextTick(()=>{
                    let all = document.getElementsByClassName('answer');
                    for(let i = 0; i < all.length; i++){
                      //如果第一个子元素div的自元素span的高度超出50， 即小组答案超出两行
                      let spanH =  all[i].childNodes[0].childNodes[0];
                      if(spanH.offsetHeight < 75){
                        all[i].setAttribute('class', 'answer hide_btn');
                      } else {
                      }
                    }
                });
				if(self.showMaxTitle == '开始打分'){
				} else {
					self.$store.state.loading = false;
				}
            }).catch(function (error) {
            })
        },
		//查看几个人已经评分
		checkRecordCount(){
			let pid = '';
			this.$axios.post('/api/play-problem',{
				node_id: this.maxNode[this.maxNodeIndex].id,
			}).then((res)=>{
//        		console.log(res)
				if(res.data.data){
					pid = res.data.data[0].problem.id;
					this.$axios.post('/api/score-user',{
						design_id: this.project_id,
						problem_id: pid
					}).then((response)=>{
//        		          console.log(response);
						let arr = []; //保存同一个id的数组  获取评分人数
						if(response.data.data){
							arr.push(response.data.data[0]);
							for(let i in response.data.data){
								if(arr[0].user_id == response.data.data[i].user_id){
									arr.push(response.data.data[i])
								}
							}
							this.scoreUserCount = arr.length-1;//减1是去除重复的第一个人
						} else {
							this.scoreUserCount = 0;
						}
						this.$store.state.loading = false;
					}).catch((err)=>{})
				}
			}).catch((error)=>{})
		},
        //显示答案隐藏详情
        maxAnswerDetail(val,status){
            for(let i in this.allAnswer){
            if(this.allAnswer[i].id == val){
              if(status){
                this.allAnswer[i].show = false;
              } else {
                this.allAnswer[i].show = true;
              }
            } else {
            }
            }
            this.showTitleA = '';
            this.showTitleA = '显示所有组答案';
        },
        //展示所有得分
        getMaxScore(){
            //获取问题id
            let val = '';
            let scoreArr = [];
            this.$axios.post('/api/play-problem',{
                node_id: this.maxNode[this.maxNodeIndex].id,
            }).then((response)=>{
//                console.log(response)
                if(response.data.data){
                    val = response.data.data[0].problem.id;
                    //获取得分
                    this.$axios.post('/api/score-node',{
                          design_id: this.project_id,
                          problem_id: val
                    }).then((response) => {
                        if(response.data.status == 0){
                            if(this.groupLists[0].name == '此演练不分组'){
                                let arr = response.data.data;
                                for(let i in response.data.data){
                                    arr[i].name = response.data.data[i].user_name;
                                }
                                scoreArr = arr;
                            } else {
                                //多组
                                for(let x in this.groupLists){
                                    for(let i in response.data.data){
                                        if(response.data.data[i].user_id == this.groupLists[x].group_user){
                                            scoreArr.push({
                                                name: this.groupLists[x].name,
                                                score: response.data.data[i].score
                                            });
                                        }
                                    }
                                }
                            }
                        }
                        this.scoreLinkGroup = scoreArr;
                        this.$store.state.loading = false;
//                        console.log(this.scoreLinkGroup)
                    }).catch((error)=>{
                    })
                }
            })
        },
        //获取该演练的总分
        getTotalPoints(){
            this.scoreMaxTotal = 0;
            for(let i in this.maxNode){
                this.$axios.post('/api/play-problem',{
                  node_id: this.maxNode[i].id
                }).then((response) => {
                    let score = response.data.data[0].problem.score;
                    this.scoreMaxTotal = this.scoreMaxTotal + score;
                }).catch((err)=>{})
            }
        },
        //显示排名
        getMaxRank(){
            let arr = [];
            this.$axios.post('/api/accord-rank',{
                design_id: this.project_id
            }).then((response) => {
                arr = response.data.data;
                for(let i=0;i<response.data.data.length;i++){
                    if(i === 0){
                      arr[i].src='./static/first.png';
                    }else if(i===1){
                      arr[i].src='./static/second.png';
                    }else if(i===2){
                      arr[i].src='./static/third.png';
                    }else if(i===3){
                      arr[i].src='./static/fourth.png';
                    }
                }
                this.maxScoreRank = arr;
                if(this.maxScoreRank.length>=4){
                  this.rankMaxIndex = "四"
                } else if(this.maxScoreRank.length == 3){
                  this.rankMaxIndex = "三";
                } else if(this.maxScoreRank.length == 2){
                  this.rankMaxIndex = "二";
                } else if(this.maxScoreRank.length == 1){
                  this.rankMaxIndex = "一";
                }
				if(this.showMaxTitle == "结束演练"){
					this.$store.state.loading = false;
				}
            }).catch((error) =>{
            })
        },
        /*开始点评（直播）*/
	    //观看端，拉流
	    //获取正在直播的流
	    getLivingFlow(){
		    this.$axios.post('/api/list-live',{
			    "prefix":'php',
			    "limit":2
		    }).then((res)=>{
//                console.log(res)
			    this.pullKey = res.data.keys[0];
			    this.getPullFlow();
		    }).catch((error)=>{
			    console.log(error);
		    })
	    },
	    //拉流
	    getPullFlow(){
		    this.$axios.post('/api/get-play',{
			    "streamKey":this.pullKey
		    }).then((res)=>{
//                console.log(res)
			    this.videoOptions.sources[0].src = res.data;
//                this.isLive =true;
		    }).catch((error)=>{
			    console.log(error);
		    })
	    },
    },

	filters: {
		markStatus1: function(value) {
			if (value == 0.0) {
				return "0";
			} else {
				return parseFloat(value).toFixed(1);
            }
		}
	},
}
</script>
